body {
  overflow-x: hidden;
  margin: 0; }

.logo_container {
  position: fixed !important;
  top: 10px;
  z-index: 100;
  left: 10px;
  width: auto !important;
  height: 100px !important; }

.logo_container img {
  background-color: #ffffff;
  padding: 20px 30px;
  border-radius: 100px; }

#logo {
  background-color: #ffffff;
  padding: 30px 30px;
  border-radius: 100px;
  max-height: none !important;
  font-family: "Roboto", Helvetica, Arial, Lucida, sans-serif;
  font-weight: 700;
  letter-spacing: 0.5px; }

#logo:hover {
  transform: scale(2.05); }

.supershadow {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.03), 0 2px 2px rgba(0, 0, 0, 0.03), 0 4px 4px rgba(0, 0, 0, 0.03), 0 8px 8px rgba(0, 0, 0, 0.03), 0 16px 16px rgba(0, 0, 0, 0.03); }

#main_nav {
  visibility: hidden;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54), border-radius 0.8s ease-in-out;
  -webkit-transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54), border-radius 0.4s ease-in-out;
  -moz-transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54), border-radius 0.4s ease-in-out;
  transform: scale(0) translateY(80px);
  position: fixed;
  opacity: 0;
  z-index: 99999;
  background-color: #fff;
  border-radius: 100px;
  width: 30% !important;
  left: 35%;
  top: 35%;
  text-align: center; }

#main_nav.active {
  visibility: visible;
  transform: scale(1) translateY(0px);
  opacity: 1;
  border-radius: 40px;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54), border-radius 0.8s ease-in-out;
  -webkit-transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54), border-radius 0.4s ease-in-out;
  -moz-transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54), border-radius 0.4s ease-in-out; }

.overlayer_menu {
  width: 0%;
  height: 0%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 99998;
  background-color: #000000;
  opacity: 0;
  transition: opacity 0.5s ease-out, width 0.01s 0.5s, height 0.01s 0.5s; }

.overlayer_menu.active {
  width: 100%;
  height: 100%;
  opacity: 0.7;
  transition: opacity 0.5s ease-out; }

#main_nav li a {
  background-color: #ffffff;
  padding: 10px 30px;
  border-radius: 0px;
  font-family: "Roboto", Helvetica, Arial, Lucida, sans-serif;
  font-weight: 700;
  letter-spacing: 0.5px;
  position: relative;
  float: left;
  width: 100%; }

#main_nav li:first-child a {
  -webkit-border-top-left-radius: 40px;
  -webkit-border-top-right-radius: 40px;
  -moz-border-radius-topleft: 40px;
  -moz-border-radius-topright: 40px;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  padding-top: 20px; }

#main_nav li:last-child a {
  -webkit-border-bottom-right-radius: 40px;
  -webkit-border-bottom-left-radius: 40px;
  -moz-border-radius-bottomright: 40px;
  -moz-border-radius-bottomleft: 40px;
  border-bottom-right-radius: 40px;
  border-bottom-left-radius: 40px;
  padding-bottom: 20px; }

#main_nav li a {
  color: #111111; }

