/* NEW MENU BUTTON */
.button_for_menu {
  display: inline-block;
  width: 167px;
  height: 80px;
  cursor: pointer;
  text-decoration: none;
  position: fixed;
  text-transform: uppercase;
  top: 10px;
  right: 10px;
  z-index: 99998;
  background-color: #ffffff;
  padding: 30px 30px;
  border-radius: 100px;
  font-family: "Roboto", Helvetica, Arial, Lucida, sans-serif;
  font-weight: 700;
  letter-spacing: 0.5px; }

.button_for_menu .line_z {
  position: relative;
  display: inline-block;
  width: 25px;
  height: 4px;
  color: #000000;
  background: #000000;
  transition: all 0.2s ease-out 0.2s;
  margin-left: 20px;
  top: -4px; }

.button_for_menu .line_z:before,
.button_for_menu .line_z:after {
  content: "";
  width: 25px;
  height: 4px;
  background: #000000;
  position: absolute;
  left: 0;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.71, 1.54); }

.button_for_menu .line_z:before {
  top: -8px; }

.button_for_menu .line_z:after {
  bottom: -8px; }

.button_for_menu.active .line_z {
  background: transparent !important; }

.button_for_menu.active .line_z:before {
  top: 0;
  transform: rotateZ(225deg); }

.button_for_menu.active .line_z:after {
  bottom: 0;
  transform: rotateZ(-225deg); }

.button_for_menu:hover .line_z,
.button_for_menu:hover .line_z:before,
.button_for_menu:hover .line_z:after {
  color: #ffffff;
  background: #ffffff; }

/* end new menu button */
